<template>
  <div id="app">
    <!--<transition name="upper">
      <router-view/>
    </transition>-->
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>

<script>
  export default {
    name: 'app'
  }
</script>

<style>
  @import './scss/_reset.scss';

  html, body {
    height: 100%;
  }

  @font-face {
    font-family: 'iconfont';  /* project id 597493 */
    src: url('http://at.alicdn.com/t/font_597493_4brnptw61v5nrk9.eot');
    src: url('http://at.alicdn.com/t/font_597493_4brnptw61v5nrk9.eot?#iefix') format('embedded-opentype'),
    url('http://at.alicdn.com/t/font_597493_4brnptw61v5nrk9.woff') format('woff'),
    url('http://at.alicdn.com/t/font_597493_4brnptw61v5nrk9.ttf') format('truetype'),
    url('http://at.alicdn.com/t/font_597493_4brnptw61v5nrk9.svg#iconfont') format('svg');
  }

  [class^="icon-"], [class*=" icon-"] {
    font-style: normal;
    font-family: 'iconfont';
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    height: 100%;
    font-size: .8rem;
  }

  .header-warpper {
    width: 100%;
    height: 2.5rem;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #c62f2f;
    overflow: hidden;
    z-index: 100;
  }

  .header-warpper > div {
    float: left;
    text-align: center;
    line-height: 2.5rem;
    color: #fff;
    font-size: 1.2rem;
  }

  .header-setting {
    width: 14%;
  }

  .header-menu {
    width: 72%;
  }

  .header-search {
    width: 14%;
  }

  .icon-music {
    font-size: 1rem;
  }

  .icon-netease {
    font-size: 1.1rem;
    margin: 0 1rem
  }

  .upper-enter-to {
    transition-duration: .2s;
  }

  .upper-enter {
    transform: translateY(60%);
    opacity: 0;
  }
</style>



