const index = resolve => require(['@/view/layout/musicLayout.vue'], resolve);
const myMusic = resolve => require(['@/view/music/myMusic.vue'], resolve);
const findMusic = resolve => require(['@/view/music/findMusic.vue'], resolve);
const songList = resolve => require(['@/view/music/songList.vue'], resolve);
const test = resolve => require(['@/view/test.vue'], resolve);
const musicSearch = resolve => require(['@/view/music/musicSearch.vue'], resolve);
const transition = resolve => require(['@/view/transition.vue'], resolve);

export default [
  {path: "/", redirect: "/index/findMusic"},
  {//主界面
    path: '/index',
    name: 'index',
    component: index,
    children: [
      {//我的音乐
        path: '/index/myMusic',
        name: 'myMusic',
        component: myMusic
      }, {//发现
        path: '/index/findMusic',
        name: 'findMusic',
        component: findMusic
      }
    ]
  },
  {//歌单页面
    path: '/songList',
    name: 'songList',
    component: songList
  },{//搜索页面
    path: '/musicSearch',
    name: 'musicSearch',
    component: musicSearch,
    meta: {
      keepAlive: true // 需要被缓存
    }
  },{//测试页面
    path: '/test',
    name: 'test',
    component: test
  },{//过渡效果测试
    path: '/transition',
    name: 'transition',
    component: transition
  }
]



